<template>
    <div class="statistic">
        <div class="title">问卷名称：{{name}}</div>
        <div class="content">
            <div class="item" v-for = "(item, index) in data" :key="index">
                <div class="title">{{item.title}}</div>   
                <div v-if="item.type == 0" class="myChart" :data-index="index" :style="{width: '200px', height: '200px'}"></div>
                <div class="list" v-if="item.type == 1">
                    <div class="list-item" v-for = "(item2, index2) in item.answerList" :key="index2">{{item2.userName}}: {{item2.content}}</div>
                </div>
            </div>
        </div>
        <div class="footer">
            <el-button @click="btnClick()" class="btn" type="primary">取消</el-button>
        </div>
    </div>
</template>

<script>
export default {
    name: "statistic",
    data() {
        return {
            loading: false,
            id: "",
            type: "",
            name: "",
            data: "",
        };
    },
    mounted() {
        this.init();
        this.search();
    },
    methods: {
        init() {
            const { type, id, name } = this.$route.query;
            this.type = type;
            this.id = id;
            this.name = name;
        },
        echartsInit() {
            const mycharts = document.querySelectorAll('.myChart');
            this.data.forEach((item, index) => {
                // 选择题
                if(item.type == 0){
                    const ansTotal = item.answerList.length;
                    let ans1 = { name: item.item1, num: 0, }, 
                        ans2 = { name: item.item2, num: 0, }, 
                        ans3 = { name: item.item3, num: 0, }, 
                        ans4 = { name: item.item4, num: 0, }, 
                        ans5 = { name: item.item5, num: 0, };

                    item.answerList.forEach(obj => {
                        if(obj.item1) ans1.num++;
                        if(obj.item2) ans2.num++;
                        if(obj.item3) ans3.num++;
                        if(obj.item4) ans4.num++;
                        if(obj.item5) ans5.num++;
                    });
                    let myChart = this.echarts.init(mycharts[index]);
                    let xAxisData = [];
                    let seriesData = [];
                    if(ans1.name) xAxisData.push(ans1.name)
                    if(ans2.name) xAxisData.push(ans2.name)
                    if(ans3.name) xAxisData.push(ans3.name)
                    if(ans4.name) xAxisData.push(ans4.name)
                    if(ans5.name) xAxisData.push(ans5.name)

                    if(ans1.num || ans1.num == 0) seriesData.push(ans1.num)
                    if(ans2.num || ans2.num == 0) seriesData.push(ans2.num)
                    if(ans3.num || ans3.num == 0) seriesData.push(ans3.num)
                    if(ans4.num || ans4.num == 0) seriesData.push(ans4.num)
                    if(ans5.num || ans5.num == 0) seriesData.push(ans5.num)

                    myChart.setOption({
                        tooltip: {},
                        xAxis: {
                            data: xAxisData
                        },
                        color: ['#409EFF'],
                        yAxis: {},
                        grid: {
                            top: 10,
                            left: 30,
                            bottom: 30,
                        },
                        series: [
                            {
                                name: "选项",
                                type: "bar",
                                data: seriesData
                            }
                        ]
                    });
                }
                // 问答题
                if(item.type == 1){

                }
            })
        },
        search() {
            //搜索
            this.loading = true;
            this.api.get(`/survey/${this.id}/answer`).then(res => {
                this.loading = false;
                this.data = res.data.questionList;
                setTimeout(()=>{
                    this.echartsInit(res);
                },20)
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },
        btnClick(){
            this.$router.go(-1)
        },
        dataInit() {
            //数据初始化
        }
    }
};
</script>
 
<style lang="less" scoped>
.statistic {
    padding: 40px;
    .title{
        font-size: 18px;
        font-weight: 600;
        padding-bottom: 30px;
    }
    .item{
        .title{
            font-size: 16px;
            padding: 0;
            margin-bottom: 10px;
        }
        .list{
            padding-top: 10px;
            height: 100px;
            overflow: auto;
            width: 50%;
            .list-item{
                padding: 5px 0;
            }
        }
    }
    .footer{
        margin-top: 20px;
    }
}
</style>
 